import signature from "../assets/signature-black.webp";
import HamburgerMenu from "react-hamburger-menu"
import "./Header.css";

const RESUME =
  "https://docs.google.com/document/d/e/2PACX-1vRz0REkDGRXf_f5P_VgXC_jD8hGxeFZWm10WVxKCrY_C0qh7iZbQhim0cIslSFvV3mfAzqrGdsq2ZET/pub";

type headerProps = {
  handleToggle(event: React.MouseEvent<HTMLButtonElement>): any;
  toggleMenu(): void;
  menu: boolean;
  about: boolean;
  portfolio: boolean;
  contact: boolean;
};

const Header = ({
  handleToggle,
  toggleMenu,
  menu,
  about,
  portfolio,
  contact,
}: headerProps) => {

  const HamburgerMenuOptions = {
    isOpen: menu,
    menuClicked() {
      toggleMenu();
    },
    width: 50,
    height: 30,
    strokeWidth: 5,
    color: menu ? "white" : "black",
    className: "hamburger",
}

  return (
    <div id="header">
      <img
        src={signature}
        id="signature"
        width="100"
        height="100"
        className={menu ? "inverted" : ""}
        alt="Morgan Stanley"
      />
      <HamburgerMenu
        {...HamburgerMenuOptions}
      />
      <nav className={`navbar ${menu ? "showNav" : ""}`}>
        <button
          id="about"
          className={`link font-playfair ${about ? "active" : ""}`}
          onClick={handleToggle}
        >
          About
        </button>
        <button
          id="portfolio"
          className={`link font-playfair ${portfolio ? "active" : ""}`}
          onClick={handleToggle}
        >
          Portfolio
        </button>
        <button
          id="contact"
          className={`link font-playfair ${contact ? "active" : ""}`}
          onClick={handleToggle}
        >
          Contact
        </button>
        <a
          className="link font-playfair"
          href="https://medium.com/@morganpstanley"
        >
          Blogâ¤¤
        </a>
        <a
          className="link font-playfair"
          href={RESUME}
          target="_blank"
          rel="noopener noreferrer"
        >
          Resumeâ¤¤
        </a>
      </nav>
    </div>
  );
};

export default Header;
